.container {
    width: 100%;
    padding-bottom: 1rem;
  }
  
  .formGroup {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .formLabel {
    margin: 1rem 0 0.7rem;
  }
  
  .formInput {
    padding: 0.5rem;
  }
  
  input[type='file'] {
    display: none;
  }
  
  .formUploadButton {
    border: 1px solid var(--primary);
    border-radius: 5px;
    text-align: center;
    width: fit-content;
    padding: 0.5rem 1rem;
    cursor: pointer;
  }
  